import React from 'react';
import styled from 'styled-components';
import Layout from './Layout';
import { partnershipList } from '../text';
import { useResizeContext } from '../context/ResizeContext';

export default function Footer() {
  const { isMobile } = useResizeContext();

  return (
    <Layout>
      <Text isMobile={isMobile}>
            JTP 
        <br />
        <strong>[   ]</strong>
        <br />
        ,    .
      </Text>
      <Partnership isMobile={isMobile}>
        {partnershipList.map((el) => (
          <img key={el.id} src={process.env.PUBLIC_URL + `/img/${el.logo}.svg`} alt={`${el.logo}  `} />
        ))}
      </Partnership>
    </Layout>
  );
}

const Text = styled.p`
  font-size: 1em;
  font-size: ${({ isMobile }) => (isMobile ? '0.75em' : '1em')};
  line-height: 1.69em;
  text-align: center;

  strong {
    font-weight: bold;
  }
`;

const Partnership = styled.div`
  display: flex;
  align-items: center;
  gap: 3.88em;
  gap: ${({ isMobile }) => (isMobile ? '1.87em' : '3.88em')};
  margin-top: 2.25em;

  img {
    width: ${({ isMobile }) => (isMobile ? '8em' : '12em')};
  }
`;
