import React, { useState, useEffect } from 'react';
import YouTube from 'react-youtube';

export default function VideoPlayer() {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const onReady = (event) => {
    event.target.playVideo();
  };

  const videoWidth = Math.min(windowWidth, 786); //  : 786px
  const videoHeight = (videoWidth / 786) * 444; //   

  useEffect(() => {
    //     windowWidth  .
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const opts = {
    height: videoHeight.toString(),
    width: videoWidth.toString(),
    playerVars: {
      autoplay: 1, // O
      rel: 0,
      modestbranding: 1, //   youtube   
      controls: 0,
      fs: 0,
      loop: 1,
      mute: 1,
    },
  };

  return (
    <div>
      <YouTube
        videoId="xXwyt8Ybqlw?si=Dtwqq_h6_xKB74aD" // YouTube  ID  .
        opts={opts}
        onReady={onReady}
      />
    </div>
  );
}
